:root {
	--global-color: red;
	--color-theme-base: black;
	--color-theme-font: white;
	--color-theme-border-color: white;
	
}
* {
  margin: 0px;
  padding: 0px;
  border: 0px;
}

.App {
  position: absolute;
  width: 100vi;
  height: 100vi;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  padding-top: 2%;
  background-color: var(--color-theme-base);

}

.view {
  margin:1%;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20%;
  background-color: var(--color-theme-font);
  color: var(--color-theme-font);
  
  background-image: url('https://img.freepik.com/vetores-premium/fichas-de-poquer-de-cassino-caindo-voando-vermelho-isoladas-no-branco-jackpot-ou-conceito-de-vencedor-textura-perfeita_3482-364.jpg');
  
  background-size: contain;
  padding-bottom: 800px;
}

table {
  position: relative;
  
  text-align: center;
  border: 1px solid black;
  background-color: black;
  opacity: 0.8;

}

tr * {
	
	border-width: 1px;
	border-style: solid;
	border-color: var(--global-color);
	border-radius: 5px;
	text-align:center;
	width:60px;
}

.btn-add {
  position: absolute;
  bottom: 0px;
  right: -35px;
  width: 31px;
  height: 27px;


  background-color: red;
  border: 2px solid var(--global-color);
  border-radius: 5px;
}
h1{color: var(--color-theme-base);}

.btn-menu {
position: fixed;
	 
	width:100%;
	height:auto;
	display: flex;
	justify-content: center;
	
}
.btn-menu *{
	margin-left:1%;
	margin-right:1%;
}
.btn-save {
  width: 13%;
  height: 40px;
  z-index: 4;
  background-color: rgba(5, 5, 5, 0.8);
  color: var(--color-theme-font);
  border-width: 1px;
	border-style: solid;
	border-color: var(--global-color);
  border-radius: 5px;
}



.btn-reset {

 
  width: 13%;
  height: 40px;
  z-index: 4;
  background-color: rgba(5, 5, 5, 0.8);
  border: 2px solid black;
  color: var(--color-theme-font);
  border-width: 1px;
	border-style: solid;
	border-color: var(--global-color);
  border-radius: 5px;
}

.enabled {
  background-color: green;
}

.jogadores-ordenados {
  height: 50px;
	
  display: flex;
  flex-direction: row;
  flex-wrap: no-wrap;
  align-items: center;
  justify-content: center;
  font-size: x-large;
  margin: 5px;
}

.jogadores-ordenados>div {
  
  background-color: black;
  
  padding: 4px;

	border-width: 1px;
	border-style: solid;
	border-color: var(--global-color);
border-radius: 5px;
  opacity: 0.8;
  margin: 1px;
 
}